* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif, serif;
  background-color: black;
}

header {
  padding: 50px 15px 0px;
}
header h1 {
  color: #fff;
  font-size: 48px;
  font-weight: 500;
  text-align: center;
}

main {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}
section {
  padding: 15px;
}
.searchbox {
  display: inline-block;
  width: 96%;
  padding: 15px;
  border: none;
  outline: none;
  background: none;
  background-color: #eee;
  border-radius: 8px;
  color: #53565a;
  font-size: 20px;
  font-weight: 300;
  transition: 0.4s ease-out;
}

.searchbox:focus {
  box-shadow: 0px 0px 8px 3px #4484c4;
}
.searchbtn {
  position: relative;
  cursor: pointer;
}
.searchbtn :hover {
  color: #4484c4;
}

.results {
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px;
}
.results .result {
  width: 33.333%;
  min-width: 250px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.result .result img {
  display: block;
  width: 100%;
}
.results .result h3 {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  padding: 10px;
  background: #161616;
  flex: 1 1 100%;
  transition: 0.4s ease-in-out;
  text-align: center;
}
.results .result:hover h3 {
  background: #fff;
  color: #223343;
}
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #161616;
  overflow-y: scroll;
}
.popup .content {
  display: block;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 25px;
  color: #bbbbbb;
}
.popup .content h2 {
  text-align: center;
  font-size: 28px;
  font-weight: 600;
}

.popup .content .plot {
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px 30px;
}
.popup .content .plot img {
  flex: 1 1 50%;
  max-width: 300px;
  padding: 0px 15px;
}
.popup .content .plot p {
  flex: 1 1 50%;
  padding: 15px;
}

.popup .info {
  margin-bottom: 30px;
  justify-content: flex-start;
}

.popup .close {
  position: absolute;
  top: 50%;
  left: 50%;
  flex-direction: row;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: 700;
  background: #4484c4;
  color: #fff;
  border-radius: 8px;
  border: none;
  outline: none;
  appearance: none;
  cursor: pointer;
  transition: 0.4s ease-out;
}
.popup .content .close:hover {
  background: #223343;
}
